/* left side */
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  background: white;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.center h1 {
  text-align: center;
  padding: 20px 0;
  border-bottom: 1px solid silver;
}
.center form {
  padding: 0 40px;
  box-sizing: border-box;
}
form .txt_field {
  position: relative;
  border-bottom: 2px solid #adadad;
  margin: 30px 0;
}
.txt_field input {
  width: 100%;
  padding: 0 5px;
  height: 40px;
  font-size: 16px;
  border: none;
  background: none;
  outline: none;
}
.txt_field label {
  position: absolute;
  top: 50%;
  left: 5px;
  color: #adadad;
  transform: translateY(-50%);
  font-size: 16px;
  pointer-events: none;
  transition: 0.5s;
}
.txt_field span::before {
  content: "";
  position: absolute;
  top: 40px;
  left: 0;
  width: 0%;
  height: 2px;
  background: #2691d9;
  transition: 0.5s;
}
.txt_field input:focus ~ label,
.txt_field input:valid ~ label {
  top: -5px;
  color: #2691d9;
}
.txt_field input:focus ~ span::before,
.txt_field input:valid ~ span::before {
  width: 100%;
}
.pass {
  margin: -5px 0 20px 5px;
  color: #a6a6a6;
  cursor: pointer;
}
.pass:hover {
  text-decoration: underline;
}

/* //---------------right side */

.experience_area .year-area h2:first-child {
  font-size: xxx-large;
  font-weight: bold;
  z-index: 2;
  color: var(--primary-color);
}

.experience_area span {
  display: block;
  font-weight: 500;
}
.experience_area .year-area {
  background-image: url(../../Images/client-info-bg.png);
  background-repeat: no-repeat;
  padding: 1rem 0rem 6rem 0rem;

  width: 30rem;

  position: relative;
  margin-bottom: 2rem;
}

.bg_panel {
  position: relative;
  left: 60px;
  top: -180px;
  width: 200px;
  height: 200px;
  background: linear-gradient(
    55deg,
    rgb(221, 221, 221) 0%,
    rgb(110, 136, 161) 100%
  );
  opacity: 0.3;
  z-index: -1;
  border-radius: 3px 25px 3px 25px;
}

@media (max-width: 500px) {
  .experience_area {
    display: none;
  }
  .gif{
    display: none;
  }

  .form {
    margin-top: 2px;
    font-size: 10px;
  }
  .formfoot {
    display: none;
  }
}
